.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

input[type="number"],
input[type="text"] {
  outline: none;
  background-color: #eef3f8;
  background-repeat: no-repeat;
  border-radius: 4px;
  height: 25px;
  min-height: 25px;
  margin: 10px auto;
  border: 2px solid transparent;
}

input[type="number"][disabled],
input[type="text"][disabled] {
  background-color: #868a8a;
}

select {
  height: 30px;
  padding-left: 10px;
  background-color: #eef3f8;
  border-radius: 4px;
}

.input-group {
  box-sizing: border-box;
  display: flex;
  margin-top: 16px;
  margin-bottom: 16px;
  flex-direction: column;
  width: 100%;
}

.input-group:has(> .input-group) {
  flex-direction: row;
}

.input-group:has(> input):has(> button) {
  flex-direction: row;
  flex-wrap: wrap;
}


.input-group:has(> input):has(> button)>input {
  flex-basis: 80%;
  border-radius: 4px 0 0 4px;
}

.input-group:has(> input):has(> button)>button {
  border-radius: 0 4px 4px 0;
  width: 30px;
  margin-top: 2px;
  border-left: 1px solid black;
}

.input-group>.input-group {
  margin-top: 0;
  margin-bottom: 0;
}

.input-group>.input-group:not(:first-child) {
  margin-left: 12px;
}

.input-group>label {
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font-size: 12px;
}

.input-group>input {
  box-sizing: border-box;
  height: 30px;
  width: 100%;
  margin-top: 2px;
  padding-left: 10px;
}

.input-group>button {
  border-radius: 4px;
  box-sizing: border-box;
  height: 30px;
  flex-grow: 1;
  border: none;
  border-left: 1px solid black;
}

.clickable {
  cursor: pointer;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modal.visible {
  display: block;
}

.modal .modal-content {
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.modal .close {
  color: #aaa;
  float: right;
  font-size: 25px;
  font-weight: bold;
  font-family: monospace;
  margin: 10px 20px;
  cursor: pointer;
}

.project-links {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
}


.github-project-link {
  left: 0;
  bottom: 0;
  margin-left: 16px;
  margin-bottom: 10px;
}

.github-project-link>img {
  height: 20px;
  width: 20px;
  opacity: 0.5;
}

.github-project-link:hover>img {
  opacity: 1;
}

.score-board {
  background-color: white;
  /* min-width: 500px; */
  padding: 20px 33px 10px 33px;
  border-radius: 20px;
}

.score-board .avatar {
  display: inline-block;
  height: 45px;
  width: 45px;
  margin-left: 10px;
  margin-right: 10px;
}

.score-board .result-description {
  font-size: 25px;
  font-weight: 800;
  margin-top: 20px;
  margin-bottom: 25px;
}

.score-board .score-board-showdown .card {
  border: 1px solid;
  box-shadow: none;
  margin-left: 2px;
  margin-right: 2px;
}

.score-board .score-board-showdown .card.ineffective {
  opacity: 0.2;
  border: 1px solid #666;
}

.score-board .score-board-showdown .community-cards {
  display: flex;
}

.score-board .score-board-showdown .hand-cards {
  display: flex;
  margin-left: 10px;
}

.score-board .score-board-table {
  width: 100%;
  font-size: 12px;
  margin: 40px auto;
  padding: 10px 40px;
  border-top: 1px dashed;
}

.score-board .score-board-table tr th {
  text-decoration: underline;
}

.score-board .score-board-table tr td {
  padding: 1px;
}

.score-board .score-board-table tr th:first-child {
  text-align: left;
}

.score-board .score-board-table tr td:first-child {
  text-align: left;
}

.score-board .score-board-table tr th:nth-child(2) {
  text-align: center;
}

.score-board .score-board-table tr td:nth-child(2) {
  text-align: center;
}

.score-board .score-board-table tr th:last-child {
  text-align: right;
}

.score-board .score-board-table tr td:last-child {
  text-align: right;
}

.score-board .score-board-table .avatar img {
  display: inline-block;
  height: 36px;
  width: 36px;
}

.score-board .avatar img {
  display: inline-block;
  height: 48px;
  width: 48px;
}

.score-board-toggle {
  position: fixed;
  right: 0;
  top: 0;

  margin-right: 20px;
  margin-top: 12px;

  cursor: pointer;
}

.score-board-toggle>img {
  height: 30px;
  width: 30px;
  opacity: 0.5;
}

.score-board-toggle:hover>img {
  opacity: 1;
  cursor: pointer;
}

.invitation .copy-link-button {
  box-sizing: border-box;
  outline: none;
  background-color: #f6f6f6;
  font-weight: bold;
  cursor: pointer;
}

.invitation .copy-link-button:hover {
  color: #f6f6f6;
  background-color: #888;
}

.invitation .qrcode-link-button {
  box-sizing: border-box;
  outline: none;
  background-color: #4a90e2;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: 13px;
  border: none;
  transition: all 0.2s;
  margin-left: 6px;
}

.invitation .qrcode-link-button:hover {
  background-color: #357abd;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(74, 144, 226, 0.3);
}

.invitation .qrcode-link-button:active {
  transform: translateY(0);
  background-color: #2a5a8a;
}

.chip {
  width: 16px;
  height: 16px;

  @media screen and (min-height: 800px) {
    width: 26px;
    height: 26px;
  }

  @media screen and (min-height: 1000px) {
    width: 30px;
    height: 30px;
  }

  transform: rotateX(30deg);
}

.card {
  width: 60px;
  height: 90px;
  /* x1.5 */
  border-radius: 6px;

  @media screen and (min-height: 800px) {
    width: 80px;
    height: 120px;
    /* x1.5 */
    border-radius: 8px;
  }

  @media screen and (min-height: 1000px) {
    width: 100px;
    height: 150px;
    /* x1.5 */
    border-radius: 10px;
  }

  background-color: #fff;
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2),
  0 1px 1px 0 rgba(0, 0, 0, 0.14),
  0 1px 3px 0 rgba(0, 0, 0, 0.12);
}

.avatar {
  display: flex;
  justify-content: center;
  border-radius: 50%;
}

.avatar.highlight {
  box-shadow: 0 0 30px white;
}

.avatar-label {
  color: #efefef;
  font-weight: bold;
  margin-top: 5px;
  margin-bottom: 5px;
}

.bankroll {
  background-color: transparent;
  border-bottom: 1px solid #f6f6f6;
  padding: 1px;
  color: white;
}

.opponents {
  position: absolute;
  left: 50%;

  top: 10px;

  @media screen and (min-height: 800px) {
    top: 30px;
  }

  @media screen and (min-height: 1000px) {
    top: 60px;
  }

  transform: translate(-50%, 0);
  display: flex;
  justify-content: space-around;
  width: 80%;
}

.opponents .opponent {
  display: inline-block;
  margin-left: 10px;
  margin-right: 10px;
  /* width: 300px; */
  /* min-width: 128px; */
}


.opponents .opponent.winner {
  border: 1px #fff solid;
  border-radius: 10px;
  padding: 5px;
  background-color: #333;
  box-shadow: 0 3px 3px -2px rgba(0, 0, 0, 0.2), 0 3px 4px 0 rgba(0, 0, 0, 0.14), 0 1px 8px 0 rgba(0, 0, 0, 0.12);
}

.opponents .opponent:first-child:not(:last-child) {
  margin-top: 30px;
}

.opponents .opponent:last-child:not(:first-child) {
  margin-top: 30px;
}

.opponents .opponent .avatar {
  margin: 10px 40px;
}

.opponents .opponent .avatar img {
  width: 48px;
  height: 48px;
}

.opponents .opponent .avatar.highlight {
  width: 48px;
  height: 48px;
}

.opponents .opponent .avatar-label {
  font-size: 12px;
  font-weight: normal;
}

.opponents .opponent .bankroll {
  margin: 10px auto;
  font-family: monospace;
}

.opponents .opponent .card {
  margin-left: 2px;
  margin-right: 2px;
  transform: rotateX(-2deg);

  width: 36px;
  /* width(.card) * 0.6 */
  height: 54px;
  /* x1.5 */

  @media screen and (min-height: 800px) {
    width: 48px;
    height: 72px;
    /* x1.5 */
  }

  @media screen and (min-height: 1000px) {
    width: 60px;
    height: 90px;
    /* x1.5 */
  }
}

.opponents .opponent:first-child:not(:last-child) .card {
  transform: rotate3d(100, -100, 0, -5deg)
}

.opponents .opponent:last-child:not(:first-child) .card {
  transform: rotate3d(-100, -100, 0, 5deg);
}

.bet-amount-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.table {
  position: absolute;
  text-align: center;

  @media screen and (max-width: 600px) {
    top: 45%;
    right: 0;
    left: 0;
    transform: translate(0, -50%);
  }

  @media screen and (min-width:600px) {
    width: 600px;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

}

.pot {
  margin-top: 2px;
  margin-bottom: 2px;
  font-size: 16px;

  @media screen and (min-height: 800px) {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 18px;
  }

  @media screen and (min-height: 1000px) {
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 20px;
  }

  display: flex;
  align-items: center;
  justify-content: center;
  font-family: monospace;
  font-weight: bold;
  color: #efefef;
}

.table .community-cards {
  perspective: 600px;
}

.table .community-cards .card {
  margin-left: 4px;
  margin-right: 4px;

  @media screen and (min-height: 800px) {
    margin-left: 8px;
    margin-right: 8px;
  }

  @media screen and (min-height: 1000px) {
    margin-left: 10px;
    margin-right: 10px;
  }

  transform-style: preserve-3d;
  transform: rotateX(5deg);
}

.bet-amount {
  font-family: monospace;
  font-size: 10px;

  @media screen and (min-height: 800px) {
    font-size: 12px;
  }

  @media screen and (min-height: 1000px) {
    font-size: 14px;
  }

  font-weight: bold;
  color: #efefef;

  margin: 2px;

  @media screen and (min-height: 800px) {
    margin: 4px;
  }

  @media screen and (min-height: 1000px) {
    margin: 10px;
  }

  display: flex;
  align-items: center;
  justify-content: center;
}

.my-seat {
  bottom: 20px;

  @media screen and (min-height: 800px) {
    bottom: 30px;
  }

  @media screen and (min-height: 1000px) {
    bottom: 40px;
  }

  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  perspective: 220px;
}

.my-seat.winner {
  border: 1px #fff solid;
  border-radius: 10px;
  padding: 8px;
  box-shadow: 0 3px 3px -2px rgba(0, 0, 0, 0.2), 0 3px 4px 0 rgba(0, 0, 0, 0.14), 0 1px 8px 0 rgba(0, 0, 0, 0.12);
  background-color: #333;
}

.my-seat .avatar {
  margin: 10px 80px;
  max-height: 80px;
}

.my-seat .avatar img {
  width: 56px;
  height: 56px;
}

.my-seat .bankroll {
  max-width: 130px;
  /* width(.card) * 2 + margin */

  @media screen and (min-height: 800px) {
    max-width: 170px;
  }

  @media screen and (min-height: 1000px) {
    max-width: 210px;
  }

  margin: 10px auto;

  font-family: monospace;
  font-size: 16px;
}

.opponents .opponent .bankroll {
  max-width: 82px;
  /* width(.card) * 2 + margin */

  @media screen and (min-height: 800px) {
    max-width: 106px;
  }

  @media screen and (min-height: 1000px) {
    max-width: 130px;
  }
}

.my-seat .card {
  margin-left: 4px;
  margin-right: 4px;
  transform: rotateX(-5deg);
}

.actions {
  margin-bottom: 25px;
  display: flex;
  justify-content: space-around;
  align-items: stretch;
}

.action-button {
  font-size: 12px;
  padding: 3px 4px;

  @media screen and (min-height: 800px) {
    font-size: 14px;
    padding: 4px 6px;
  }

  @media screen and (min-height: 1000px) {
    font-size: 16px;
    padding: 6px 8px;
  }

  margin-left: 5px;
  margin-right: 5px;

  justify-content: center;
  outline-width: 0;
  text-align: center;
  min-width: 64px;
  border: 1px #f6f6f6 solid;
  background-color:rgba(0, 0, 0, 0.2);
  color: #f6f6f6;
  border-radius: 10px;
  cursor: pointer;
  font-family: monospace;
  font-weight: bold;
}

.action-button:not([disabled]):hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.start-button {
  margin-top: 5px;
}

.staging {
  margin: 10px;
  padding: 10px 40px;
  color: #f6f6f6;

  @media screen and (min-height: 800px) {
    margin: 16px;
    padding: 16px;
  }

  @media screen and (min-height: 1000px) {
    margin: 20px;
    padding: 20px;
  }
}

.staging.host {
  box-shadow: 0 3px 3px -2px rgba(0, 0, 0, 0.2), 0 3px 4px 0 rgba(0, 0, 0, 0.14), 0 1px 8px 0 rgba(0, 0, 0, 0.12);
  border-radius: 10px;
  background-color: #333;
}

.staging h4 {
  font-size: 16px;
  margin-top: 10px;
  margin-bottom: 24px;
}

.staging hr {
  margin-top: 24px;
  margin-bottom: 24px;
}

.staging .action-button {
  font-size: 16px;
  padding: 10px;
  margin-top: 16px;
  text-transform: uppercase;
}

.message-bar {
  position: fixed;
  right: 0;
  bottom: 0;

  margin-right: 10px;
  background: #fff;
  border-radius: 8px 8px 0 0;
  box-shadow: 0 0 5px 1px #3333;

  transition: all 0.3s;

  height: 450px;

  @media screen and (max-width: 500px) {
    width: 150px
  }

  @media screen and (min-width: 500px) {
    min-width: 300px;
    max-width: 30vw;
  }

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.message-bar.collapsed {
  bottom: -405px;
  /* - height(.message-bar) + 45px */
}

.message-bar .title-bar {
  height: 45px;
  display: flex;
  justify-content: space-between;
  padding: 0 5px;
  border-bottom: 1px solid #f1f1f1;
}

.message-bar .title-bar:hover {
  cursor: pointer;
}

.message-bar .title-bar .profile {
  display: flex;
  align-items: center;
  user-select: none;
}

.message-bar .title-bar .profile h4 {
  font-size: 14px;
  font-weight: bold;
  margin-left: 7px;
}

.message-bar .title-bar .profile .avatar {
  margin-left: 5px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.message-bar .title-bar .icon>p {
  margin-right: 15px;
  font-weight: bold;
  transition-property: background;
  transition-duration: 0.3s;
  transform: rotate(-90deg);
}

.message-bar .title-bar .badge {
  margin-left: 7px;
  background: #ffeb90;
  color: #111;
}

.message-bar .no-messages {
  font-size: 12px;
  color: #333;
}

.message-bar .messages {
  /*height: 230px; !* height(.message-bar) - height(.title-bar) height(.message-input) *!*/
  margin-top: auto;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

.message-bar .message {
  font-size: 12px;
  font-family: monospace;
  text-align: left;
  background-color: white;
  padding: 2px 10px;
  margin-bottom: 10px;

  display: flex;
  align-items: center;
}

.message-bar .message.mime {
  justify-content: flex-end;
}

.message-bar .message .private-message {
  margin-left: 2px;
  color: #666;
}

.message-bar .message .card-char {
  font-size: 30px;
  color: black;
}

.message-bar .message .card-char.diamond {
  color: red;
}

.message-bar .message .card-char.heart {
  color: red;
}

.message-bar .message .avatar img {
  display: inline-block;
  height: 25px;
  width: 25px;
  margin-bottom: 5px;
  margin-right: 10px;
}

.message-bar .message .message-text {
  max-width: 250px;
  word-wrap: break-word;
}

.message-bar .message .name-and-message-text .name {
  color: #666;
  font-size: 10px;
  margin-bottom: 2px;
  max-width: 250px;
  word-wrap: break-word;
}

.message-bar .message.mime {
  text-align: right;
}

.message-bar .message.system-notification {
  justify-content: center;
  font-size: 10px;
  color: #888;
}

.message-bar .message.system-notification::before {
  content: '⠿';
  margin-right: auto;
}

.message-bar .message.system-notification::after {
  content: '⠿';
  margin-left: auto;
}

.message-bar .message-input {
  padding: 0 0 0 10px;
  width: 85%;
}

.name-input {
  text-align: center;
}

.badge {
  background-color: #999;
  display: inline-block;
  padding: 5px;
  font-size: 10px;
  font-weight: 800;
  color: #fff;
  border-radius: 5px;
}

.qrcode-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9998;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.qrcode-modal-content {
  background: white;
  border-radius: 12px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  width: 90%;
  max-width: 400px;
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    transform: translateY(50px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.qrcode-modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid #f0f0f0;
}

.qrcode-modal-header h2 {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  color: #333;
}

.qrcode-modal-close {
  background: none;
  border: none;
  font-size: 24px;
  color: #999;
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: all 0.2s;
}

.qrcode-modal-close:hover {
  background-color: #f5f5f5;
  color: #333;
}

.qrcode-modal-body {
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

.qrcode-container {
  padding: 16px;
  background-color: #f9f9f9;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.qrcode-container canvas {
  max-width: 100%;
  height: auto;
}

.qrcode-hint {
  margin: 0;
  font-size: 14px;
  color: #666;
  text-align: center;
}

.qrcode-link-display {
  width: 100%;
  background-color: #f5f5f5;
  padding: 12px;
  border-radius: 6px;
}

.qrcode-link-label {
  margin: 0 0 8px 0;
  font-size: 12px;
  font-weight: 600;
  color: #999;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.qrcode-link-input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 12px;
  font-family: monospace;
  box-sizing: border-box;
  background-color: white;
  color: #333;
  cursor: pointer;
  transition: all 0.2s;
}

.qrcode-link-input:hover {
  border-color: #999;
}

.qrcode-link-input:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.1);
}

.qrcode-modal-footer {
  display: flex;
  gap: 10px;
  padding: 16px 20px;
  border-top: 1px solid #f0f0f0;
  justify-content: flex-end;
}

.qrcode-download-btn {
  padding: 8px 16px;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  font-size: 13px;
  cursor: pointer;
  transition: all 0.2s;
}

.qrcode-download-btn:hover {
  background-color: #357abd;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(74, 144, 226, 0.3);
}

.qrcode-download-btn:active {
  transform: translateY(0);
  box-shadow: 0 2px 4px rgba(74, 144, 226, 0.3);
}

.qrcode-close-btn {
  padding: 8px 16px;
  background-color: #f0f0f0;
  color: #333;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  font-size: 13px;
  cursor: pointer;
  transition: all 0.2s;
}

.qrcode-close-btn:hover {
  background-color: #e0e0e0;
}

.qrcode-close-btn:active {
  background-color: #d0d0d0;
}

/* 响应式设计 */
@media (max-width: 480px) {
  .qrcode-modal-content {
    width: 95%;
    max-width: 100%;
  }

  .qrcode-modal-header {
    padding: 16px;
  }

  .qrcode-modal-header h2 {
    font-size: 18px;
  }

  .qrcode-modal-body {
    padding: 20px 16px;
  }

  .qrcode-modal-footer {
    padding: 12px 16px;
    flex-direction: column;
  }

  .qrcode-download-btn,
  .qrcode-close-btn {
    width: 100%;
    padding: 10px;
  }
}